import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Hint from './components/Hint/Hint'
import PageRouter from './pages/PageRouter';
import { useLocation } from 'react-router-dom'
import './app.less';
import './assets/css/fonts.less'
import { inject, observer } from 'mobx-react';

function App({intl}) {
  const location = useLocation();
  const curRouterClass = location.pathname.split('/')[1]
  const trade = location.pathname.split('/')[2]
  return (
    <div className={`deri-root ${curRouterClass}  ${trade}`}>
      {/* {curRouterClass === 'pool' && <Hint/>}  */}
      <Header></Header>
      <PageRouter intl={intl}></PageRouter>
    </div>
  );
}

export default inject('intl')(observer(App))
