import React from 'react';
import {Switch,Route,withRouter} from 'react-router-dom'
import LoadableComponent from '../utils/LoadableComponent';
import withI18n from '../hoc/withI18n';
import IndexRoute from '../components/IndexRoute/IndexRoute';
import Type from '../model/Type';
import { FUTURE, OPTION } from '../utils/Constants';
import { inject, observer } from 'mobx-react';


const Home = LoadableComponent(() => import('./Home/Home')) 
const Pool = LoadableComponent(() => import('./Pool/Index')) 
const Retired = LoadableComponent(() => import('./Retired/Retired')) 
const PoolDetail = LoadableComponent(() => import('./Pool/Detail')) 
const Trade = LoadableComponent(() => import('./Trade/Trade'))
const Bridge = LoadableComponent(() => import('./Bridge/Bridge'))
const Governance = LoadableComponent(() => import('./Governance/Governance'))
const Token = LoadableComponent(() => import('./Token/Token'))
const Info = LoadableComponent(() => import('./Info/Index'))
const Team = LoadableComponent(() => import('./Team/Team'))
const InfoDetail = LoadableComponent(() => import('./Info/Detail'))
const TradingMining = LoadableComponent(() => import('./TradeToEarn/index'))
const TradingMiningFinishedThree = LoadableComponent(() => import('./TradeToEarn/cycle/three'))
const DipOne = LoadableComponent(() => import('./Governance/DipOne'))
const DipTwo = LoadableComponent(() => import('./Governance/DipTwo'))
const AirDrop = LoadableComponent(()=> import('./AirDrop/AirDrop'))
const Faucet = LoadableComponent(()=> import('./Faucet/Faucet'))
const Practice = LoadableComponent(()=> import('./Practice/Practice'))

const TradeRender = ({dict,type,getLang}) => {
  Type.setCurrent(type)
  return <Trade lang={dict['trade']} getLang={getLang} />
}

@withRouter
@withI18n
class PageRouter extends React.Component{

  getLang(page,key,params,options){
    return this.props.intl.eval(page,key,params,options)
  }

  render(){
    const {dict} = this.props  

    return (
      <Switch>
        <Route exact path='/index' component={() => <Home lang={dict['home']} />}></Route>
        <Route exact path='/faucet' component={() => <Faucet lang={dict['faucet']} />}></Route>
        <Route exact path='/practice-to-earn' component={() => <Practice lang={dict['practice']} />}></Route>
        <Route exact path='/team' component={() => <Team lang={dict['team']} />}></Route>
        <Route exact path='/pool' component={() => <Pool lang={dict['pool']} getLang={(key,params,options) => this.getLang('pool',key,params,options)} />}></Route>
        <Route exact path='/retired' component={() => <Retired lang={dict['pool']} getLang={(key,params,options) => this.getLang('pool',key,params,options)} />}></Route>
        <Route exact path='/pool/:version/:chainId/:type/:address' component={() => <PoolDetail lang={dict['pool']} getLang={(key,params,options) => this.getLang('pool',key,params,options)} />}/>
        <Route exact path='/trade/futures' render={props => <TradeRender dict={dict} type={FUTURE} getLang={(key,params,options) => this.getLang('trade',key,params,options)} />}/>
        <Route exact path='/trade/options' render={props => <TradeRender dict={dict} type={OPTION} getLang={(key,params,options) => this.getLang('trade',key,params,options)} />}/>
        <Route exact path='/trade/futures/retired' render={props => <TradeRender dict={dict} type={FUTURE} getLang={(key,params,options) => this.getLang('trade',key,params,options)} />}/>
        <Route exact path='/trade/options/retired' render={props => <TradeRender dict={dict} type={OPTION} getLang={(key,params,options) => this.getLang('trade',key,params,options)} />}/>
        <Route exact path='/bridge' component={() => <Bridge lang={dict['bridge']} />}></Route>
        <Route exact path='/airdrop' component={() => <AirDrop lang={dict['airdrop']} />}></Route>
        <Route exact path='/governance' component={() => <Governance lang={dict['governance']} />}></Route>
        <Route exact path='/dipone' component={() => <DipOne lang={Object.assign(dict['governance'],dict['dip-one'])} />}></Route>
        <Route exact path='/diptwo' component={() => <DipTwo lang={Object.assign(dict['governance'],dict['dip-two'])} />}></Route>
        <Route exact path='/token' component={() => <Token lang={dict['token']} />}></Route>
        <Route exact path='/info' component={() => <Info lang={dict['info']} />}></Route>
        <Route exact path='/info/:add/:catalog/:bToken/:network' component={() => <InfoDetail/>}/>
        <Route exact path='/trade-to-earn' component={() => <TradingMining lang={dict['trading-mining']}/>}/>
        <Route exact path='/trade-to-earn-finished' component={() => <TradingMiningFinishedThree lang={dict['trading-mining']}/>}/>
        <Route exact path='/' render={() => <IndexRoute/>}/>
      </Switch>
    )
  }
}
export default PageRouter